import React from 'react';
import { Button, Modal } from 'react-bootstrap';

export default class TermsAndConditions extends React.Component {
  constructor () {
    super();

    this.toggleAgreed = this.toggleAgreed.bind(this);

    this.state = {
      agreed: false
    };
  }

  toggleAgreed (event) {
    this.setState({ agreed: event.target.checked });
  }

  render () {
    const {
      HideTermsAndConditionsHandler,
      ConfirmTermsAndConditionsHandler,
      ConfirmedTermsAndConditions,
      TermsAndConditions
    } = this.props;

    const showModal = (ConfirmedTermsAndConditions !== true);

    let honninUrl = '';
    let oshiireKojinJouhouUrl = '';
    let oshiireYakkanUrl = '';
    let kojinJouhouUrl = '';
    let hoshouYakkanUrl = '';

    if (TermsAndConditions !== undefined) {
      honninUrl = TermsAndConditions.honnin === undefined ? '' : TermsAndConditions.honnin;
      oshiireKojinJouhouUrl = TermsAndConditions.oshiire_kojin_jouhou === undefined ? '' : TermsAndConditions.oshiire_kojin_jouhou;
      oshiireYakkanUrl = TermsAndConditions.oshiire_yakkan === undefined ? '' : TermsAndConditions.oshiire_yakkan;
      kojinJouhouUrl = TermsAndConditions.kojin_jouhou === undefined ? '' : TermsAndConditions.kojin_jouhou;
      hoshouYakkanUrl = TermsAndConditions.hoshou_yakkan === undefined ? '' : TermsAndConditions.hoshou_yakkan;
    }

    return (
      <Modal show={showModal} onHide={() => {}}>
        <Modal.Header>
          <Modal.Title><strong>利用規約確認</strong></Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>
            <strong>【 お申し込みの前に 】</strong>
            <br />
            <br />
            <strong>①ご本人確認書類の写真（JPEG、PDF等）のご準備をお願い致します。</strong>
            <br />
            <br />
            <a href={honninUrl} target='_blank' rel='noopener noreferrer'>本人確認書類一覧</a>
            <br />
            <br />
            <strong>②下記規約等をよくお読み頂き、同意して頂けましたら「同意する」をクリックしてお進みください。</strong>
          </p>

          <ul className='list-unstyled'>
            <li><a href={oshiireKojinJouhouUrl} target='_blank' rel='noopener noreferrer'>個人情報保護について</a></li>
            <li><a href={oshiireYakkanUrl} target='_blank' rel='noopener noreferrer'>レンタルスペース一時使用契約約款</a></li>
            <li><a href={kojinJouhouUrl} target='_blank' rel='noopener noreferrer'>個人情報保護方針</a></li>
            <li><a href={hoshouYakkanUrl} target='_blank' rel='noopener noreferrer'>保証委託契約約款</a></li>
          </ul>

          <div className='checkbox'>
            <label>
              <input type='checkbox' id='check' defaultChecked={false} onChange={this.toggleAgreed.bind(this)} /> 上記規約に同意する
            </label>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button onClick={HideTermsAndConditionsHandler}>前へ戻る</Button>
          <Button bsStyle='primary' disabled={!this.state.agreed} onClick={ConfirmTermsAndConditionsHandler}>次へ</Button>
        </Modal.Footer>
      </Modal>
    );
  }
}
