import React from 'react';
import FormGroup from '../FormGroup';
import PhoneNumber from '../PhoneNumber';
import AlertText from '../AlertText';
import * as Const from '../../Utils/Const';

export default class UserInfoPersonal extends React.Component {
  render () {
    const {
      PersonalInfoItem,
      PersonalInfoHandlers
    } = this.props;

    return (
      <div id='kokyaku_personal'>
        <FormGroup title='氏名' isRequired>
          <div className='row'>
            <div className='col-sm-6 col-xs-6'>
              <div className='input-group'>
                <span className='input-group-addon'>姓</span>
                <input
                  type='text'
                  name='lastName'
                  value={PersonalInfoItem.lastName}
                  onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
                  size='20'
                  maxLength='40'
                  className='form-control'
                />
              </div>
            </div>
            <div className='col-sm-6 col-xs-6'>
              <div className='input-group'>
                <span className='input-group-addon'>名</span>
                <input
                  type='text'
                  name='firstName'
                  value={PersonalInfoItem.firstName}
                  onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
                  size='20'
                  maxLength='40'
                  className='form-control'
                />
              </div>
            </div>

          </div>
        </FormGroup>

        <FormGroup title='フリガナ' isRequired>
          <div className='row'>
            <div className='col-sm-6 col-xs-6'>
              <div className='input-group'>
                <span className='input-group-addon'>セイ</span>
                <input
                  type='text'
                  name='lastNameKana'
                  value={PersonalInfoItem.lastNameKana}
                  onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
                  size='40'
                  maxLength='60'
                  className='form-control'
                />
              </div>
            </div>
            <div className='col-sm-6 col-xs-6'>
              <div className='input-group'>
                <span className='input-group-addon'>メイ</span>
                <input
                  type='text'
                  name='firstNameKana'
                  value={PersonalInfoItem.firstNameKana}
                  onChange={PersonalInfoHandlers.updatePersonalInfoHandler.bind(this)}
                  size='40'
                  maxLength='120'
                  className='form-control'
                />
              </div>
            </div>
          </div>
        </FormGroup>

        <FormGroup title='メールアドレス' isRequired>
          <input
            type='email'
            name='kokyaku_email'
            value={PersonalInfoItem.email}
            onChange={PersonalInfoHandlers.updateEmailHandler.bind(this)}
            size='40'
            maxLength='60'
            className='form-control ime-mode-disabled'
          />
          {
            PersonalInfoItem.isValidEmail === false &&
              <AlertText text='メールアドレスが正しくありません。' />
          }
        </FormGroup>

        <FormGroup title='マイページログインパスワード' isRequired>
          <form>
            <input
              type='password'
              name='kokyaku_password'
              // value={PersonalInfoItem.password}
              onChange={PersonalInfoHandlers.updatePasswordHandler.bind(this)}
              size='40'
              maxLength='60'
              className='form-control ime-mode-disabled'
            />
          </form>
          {
            PersonalInfoItem.isValidPassword === false &&
              <AlertText text='パスワードが正しくありません。' />
          }
          <p className='help-block'>半角英数7文字以上</p>
        </FormGroup>

        <FormGroup title='ご連絡先' isRequired>
          <PhoneNumber
            title='携帯'
            phoneType={Const.PhoneType.MobilePhone}
            phone1={PersonalInfoItem.mobilePhone1}
            phone2={PersonalInfoItem.mobilePhone2}
            phone3={PersonalInfoItem.mobilePhone3}
            updatePhoneHandler={PersonalInfoHandlers.updatePhoneHandler}
          />
          {
            PersonalInfoItem.isValidMobilePhone === false &&
              <AlertText text='半角数字を入力してください。' />
          }
        </FormGroup>

      </div>
    );
  }
}
